import { Link } from "react-router-dom";
import CarrosselDiscovery from "./CarrosselDiscovery";

export default function DiscoveryPlaces() {
  const list = [
    "Descubra um mundo de novos sabores autnticos e surpreendentes da culinria nordestina em nossos restaurantes e bares, onde cada prato  uma verdadeira experincia gastronmica.",
    "Visite nossos restaurantes da terra e sinta o verdadeiro sabor do Nordeste, com pratos preparados com ingredientes frescos e regionais que trazem o gosto autntico das nossas razes.",
    "Explore os bares autnticos do Nordeste, onde voc pode desfrutar de bebidas tpicas, petiscos deliciosos e um ambiente vibrante que celebra a cultura e a alegria da nossa regio.",
    "Prepare-se para ficar com gua na boca ao conhecer nosso guia de restaurantes e bares nordestinos, onde cada prato  uma tentao e cada visita uma nova descoberta culinria."
  ];

  return (
    <div className="section discovery-places">
      <div className="col img">
        <CarrosselDiscovery /> 
      </div>
      <div className="col typography">
        <h1 className="tittle">Indico sabor</h1>
        {list.map((item, index) => (
          <p className="discovery-item" key={index}>{item}</p>
        ))}
        <Link to="/indications" className="btn">Descubra agora</Link>
      </div>
    </div>
  );
}