import CustomImage from "../CostumeImage";
import { Link } from "react-router-dom";

export default function HeroSection() {
  const images = [
    "./embarqueDigital20241/img/home/image_1.jpg",
    "./embarqueDigital20241/img/home/image_2.jpg",
    "./embarqueDigital20241/img/home/image_3.jpg",
    "./embarqueDigital20241/img/home/image_4.jpg",
    "./embarqueDigital20241/img/home/image_5.jpg",
    "./embarqueDigital20241/img/home/image_6.jpg",
    "./embarqueDigital20241/img/home/image_7.jpg",
    "./embarqueDigital20241/img/home/image_8.jpg",
    "./embarqueDigital20241/img/home/image_9.jpg",
  ];
  return (
    <div className="section hero">
      <div className="col typography">
        <h1 className="tittle">Receitas arretadas e mais!</h1>
        <p className="info">Somos um portal para um mundo de sabores, aromas e experincias culinrias inesquecveis. Aqui, voc encontrar uma seleo exclusiva de receitas do Nordeste brasileiro, uma regio rica em tradies gastronmicas que encantam o paladar. Explore pratos icnicos, desde o acaraj da Bahia at o baio de dois do Cear, e deixe-se envolver pelas histrias e culturas que fazem dessas receitas algo to especial. Alm disso, oferecemos um catlogo selecionado de bares e restaurantes, cuidadosamente escolhidos para proporcionar a voc o melhor da culinria nordestina. Venha descobrir e saborear a autntica cozinha do Nordeste atravs do nosso portal, uma verdadeira viagem culinria que voc no vai querer perder.</p>
        <Link to="/recipes" className="btn">Confira aqui</Link>
      </div>
      <div className="col gallery">
        {images.map((src, index) => (
          <CustomImage key={index} imgSrc={src} pt={"75%"} />
        ))}
      </div>
    </div>
  );
}